




















import Vue, { PropOptions } from "vue";
export default Vue.extend({
  name: "Tooltip",
  data() {
    return {
      value: false,
      innerClass: "",
      bottom: true,
      left: false,
      right: false,
      top: false,
      forcePositionMarginLeft: undefined as string | undefined,
      forcePositionBottom: undefined as string | undefined,
      forcePositionRight: undefined as string | undefined,
      forcePositionLeft: undefined as string | undefined,
      forcePositionMarginTop: undefined as string | undefined
    };
  },
  props: {
    toolTipContent: {
      default: "",
      type: String,
      note: "",
    } as PropOptions,
    customClass: {
      type: Object,
      note: "Use to set class font size of field",
    } as PropOptions,
    position: {
      default: "bottom",
      type: String,
      note: "Aligns the component",
    } as PropOptions,
    disabled: {
      default: false,
      type: Boolean,
      note: "Disables the tooltip",
    } as PropOptions,
    openDelay: {
      type: [Number, String],
    },
    transition: {
      type: String,
    },
    maxWidth: {
      type: [Number, String],
    },
    minWidth: {
      type: [Number, String],
    },
    nudgeWidth: {
      type: [Number, String],
    },
    nudgeBottom: {
      type: [Number, String],
    },
    nudgeTop: {
      type: [Number, String],
    },
    nudgeLeft: {
      type: [Number, String],
    },
    nudgeRight: {
      type: [Number, String],
    },
    allowOverflow: {
      type: [Boolean],
    },
    attach: {
      type: String,
    },
    absolute: {
      type: Boolean,
    },
    forcePosition: {
      type: Boolean,
    },
  },
  mounted() {
    const self = this as unknown as {
      classes: Record<string, any> | undefined;
    }; // classes diye bişi var mı yok mu bilinmiyor. aşağıdaki satır komple anlamsız olabilir.

    if (self.classes) {
      this.innerClass = Object.keys(self.classes).join(" ");
    }
    // this.calculatePositon();
    setTimeout(() => {
      if (this.position != "bottom") {
        this.bottom = false;
        switch (this.position) {
          case "left":
            this.left = true;
            this.forcePositionRight = "calc(100% + 10px)";
            this.forcePositionBottom = (<Element>this.$children[0].$el).clientHeight > 35 ? `35%` : "";
            break;
          case "right":
            this.right = true;
            this.forcePositionLeft = "calc(100% + 10px)";
            this.forcePositionBottom =(<Element>this.$children[0].$el).clientHeight > 35 ? `35%` : "";
            break;
          case "top":
            this.top = true;
            this.forcePositionLeft = "50%";
            this.forcePositionMarginLeft = `-${(<Element>this.$refs["tooltip-force-position-content"]).clientWidth / 2}px`;
            this.forcePositionMarginTop = `-${(<Element>this.$refs["tooltip-force-position-content"]).clientHeight + 10}px`;
            break; 
          default:
            break;
        }
      } else {
        this.forcePositionLeft = "50%";
        this.forcePositionMarginLeft = `-${(<Element>this.$refs["tooltip-force-position-content"]).clientWidth / 2}px`;
        this.forcePositionMarginTop = `${(<Element>this.$children[0].$el).clientHeight + 10}px`;
      }
    }, 0);
  },
  destroyed() {
    this.$el?.remove();
  }
});
